@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:wght@400;500&display=swap');

.brain-section {
  min-height: 100vh;
  background: #000;
  color: white;
  padding: 4rem 2rem;
  position: relative;
}

.brain-content {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  opacity: 1;
}

.brain-title {
  font-family: 'Cormorant Garamond', serif;
  font-size: 24px;
  opacity: 1;
  text-align: center;
  margin-bottom: 1rem;
  font-weight: 400;
}

.brain-subtitle {
  font-family: 'Cormorant Garamond', light;
  font-size: 16px;
  line-height: 1.2;
  max-width: 750px;
  margin: 0 auto 3rem;
  opacity: 1;
  text-align: center;
  color: rgb(186, 186, 186);
}

.brain-container {
  position: relative;
  width: 100%;
  max-width: 1200px;
  height: 450px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.brain-image {
  width: 400px;
  height: auto;
  opacity: 0.9;
}

.idea-block {
  position: absolute;
  width: 300px;
}

.idea-block.left {
  right: calc(50% + 200px);
  text-align: right;
}

.idea-block.right {
  left: calc(50% + 200px);
  text-align: left;
}

/* Adjust positions to match the image */
.idea-block:nth-child(1) { top: 20%; }  /* AI that learns humor */
.idea-block:nth-child(2) { top: 40%; }  /* Smart rings */
.idea-block:nth-child(3) { top: 60%; }  /* Blockchain */
.idea-block:nth-child(4) { top: 80%; }  /* Hyperlocal AI */

.idea-line {
  position: absolute;
  height: 2px;
  background: #4ecdc4;
  width: 80px;
}

.idea-block.left .idea-line {
  right: -80px;
}

.idea-block.right .idea-line {
  left: -80px;
}

.idea-content {
  padding: 0rem;
}

.idea-content h3 {
  font-family: 'EB Garamond', serif;
  font-size: 16px;
  margin: 0.25rem 0rem 0.25rem;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.95);
}

.idea-content p {
  font-family: 'EB Garamond', serif;
  font-size: 14px;
  margin: 0.25rem 0rem 0.25rem;
  line-height: 1.2;
  color: rgba(255, 255, 255, 0.7);
}

/* Connection points */
.idea-block::after {
  content: '';
  position: absolute;
  width: 6px;
  height: 6px;
  background: #4ecdc4;
  border-radius: 50%;
  top: 50%;
  box-shadow: 0 0 10px #4ecdc4;
}

.idea-block.left::after {
  right: -83px;
  transform: translateY(-50%);
}

.idea-block.right::after {
  left: -83px;
  transform: translateY(-50%);
}

/* Animations */
.idea-block.visible .idea-line {
  animation: pulseLine 2s infinite;
}

.idea-block.visible::after {
  animation: pulsePoint 2s infinite;
}

@keyframes pulseLine {
  0%, 100% { opacity: 0.4; }
  50% { opacity: 1; }
}

@keyframes pulsePoint {
  0%, 100% {
    transform: translateY(-50%) scale(1);
    opacity: 0.4;
  }
  50% {
    transform: translateY(-50%) scale(1.5);
    opacity: 1;
  }
}

/* Remove transition classes that were causing visibility issues */
.brain-title.visible,
.brain-subtitle.visible {
  opacity: 1;
  transform: none;
}
